'use strict';

/**
* Generate a logarithmically spaced numeric array.
*
* @module @stdlib/array-logspace
*
* @example
* var logspace = require( '@stdlib/array-logspace' );
*
* var arr = logspace( 0, 2, 6 );
* // returns [ 1, ~2.5, ~6.31, ~15.85, ~39.81, 100 ]
*/

// MODULES //

var main = require( './main.js' );


// EXPORTS //

module.exports = main;
